import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ManageTrainerListComponent } from './manage-trainer-list.component';
import { ManageTrainerAddEditComponent } from './manage-trainer-add-edit/manage-trainer-add-edit.component';
import { ManageTrainerListRoutingModule } from './manage-trainer-list.routing';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';
import { ImageCropModule } from 'imageCroppingPopup/imageCrop.module';
import { ImageUploadModule } from 'ng2-imageupload';
import { Ng2CompleterModule } from '../core/autocomplete/ng2-completer.module';
import { CommonComponentsModule } from 'common/common.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ManageTrainerListRoutingModule,
    ReactiveFormsModule,
    CoreModule,
    ImageCropModule,ImageUploadModule,    Ng2CompleterModule,CommonComponentsModule

  ],
  declarations: [ManageTrainerListComponent, ManageTrainerAddEditComponent],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class ManageTrainerListModule { }



