import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { Constants } from 'core/constants';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { TrainerGymDetail } from 'core/models/gymtrainerDetailResponse';
import { LocalStorageService } from 'core/service/local-storage.service';

@Component({
  selector: 'app-manage-trainer-list',
  templateUrl: './manage-trainer-list.component.html',
  styleUrls: ['./manage-trainer-list.component.css']
})
export class ManageTrainerListComponent implements OnInit,HttpSuccesFailureResponse {

  currentPage = 0;
  trainerList: TrainerGymDetail[] = [];
  headerTitleGYMRegular=[{name:"Trainer Name",view:false,id:0},{name:"Activities",view:false,id:0},{name:"Email",view:false,id:0},{name:"Phone Number",view:false,id:0},{name:"Pay model",view:false,id:0},{name:"Action",view:false,id:0}]
  listIsLaoding: boolean = false;

  constructor(private globalSubscription: GlobalSubscriptionService, private _activatedRoute: ActivatedRoute, private router: Router, private globalSubscriptionService: GlobalSubscriptionService, private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService) { }
 

  ngOnInit() {
    this.trainerListByGym();
  }
  trainerListByGym() {
    this.listIsLaoding = true;
    this.http_userauth.getAllTrainerListNewGym(LocalStorageService.getUserId(), this.currentPage, this, true)
 
}
routeToTrainerDetails(value?: TrainerGymDetail) {
  if(value){
    this.router.navigate(['/manageTrainer', value.id]);

  }else{
    this.router.navigate(['/manageTrainer/add']);

  }

}
onSuccess(type: any, responsedata: any) {
  if (type === Constants.AllTrainerApi) {
    this.listIsLaoding = false;
    const temp_list: TrainerGymDetail[] = responsedata;
    temp_list.forEach(element => {
       this.trainerList.push(element);
     });

    



}
}
onFailure(type: any, response: any) {
  if (type === Constants.AllTrainerApi) {
    this.listIsLaoding = false;




}
}
}



