import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageTrainerListComponent } from './manage-trainer-list.component';
import { ManageTrainerAddEditComponent } from './manage-trainer-add-edit/manage-trainer-add-edit.component';


const routes: Routes = [
    { path: '', component: ManageTrainerListComponent },
    {path:':id',component:ManageTrainerAddEditComponent},

    {path:'add',component:ManageTrainerAddEditComponent},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManageTrainerListRoutingModule { }



