import { ClassCategoryList } from 'core/models/trainerDetailResponse';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';

@Component({
  selector: 'app-manage-trainer-add-edit',
  templateUrl: './manage-trainer-add-edit.component.html',
  styleUrls: ['./manage-trainer-add-edit.component.css']
})
export class ManageTrainerAddEditComponent implements OnInit,HttpSuccesFailureResponse {


  trainerForm: FormGroup;
  userData: any;
  currentTrainerId: any;
  packageData: any = [];


  imageCropperSettings: CropperSettings = new CropperSettings();
  format = false;
  uploadfailed = false;
  uploadImageProgess = false;
  userImage: string;
  profileFileList: File[] = [];
  profileMainFile: any;
  profilefilelength = false;
  uploadError = '';
  @ViewChild('fileInput') fileInput: ElementRef;


  dataService: CompleterData;
  activitiesListDataResponse: ActivitiesListData[] = [];
  deletedActivitiesList: number[] = [];
  activitiesList: ClassCategoryList[] = [];
  deletedActivitiesList1: number[] = [];
  isFieldValid = false;
  userDetails:any;

  commisionTypeArray = [{id:'PERCENTAGE',name:'Percentage'},{id:'FIXED',name:'Fixed'}];
  adjustTypeArray = [{value:true,name:'Yes'},{value:false,name:'No'}];
  headerTitleGYMRegular=[{name:"Package",view:false,id:0},{name:"Revenue Per Session",view:false,id:0},{name:"Commision Type",view:false,id:0},{name:"Commision Value",view:false,id:0},{name:"Adjust Based On Discount",view:false,id:0},{name:"Commision Amount",view:false,id:0}]
  subcategoryDataFromApi: any;
  letSelectedImage:any;

  headerTitlePerSession=[{name:"Class Sub Category",view:false,id:0},{name:"Per session pay",view:false,id:0}]

  gender = Constants.genderRegister;
  public gymName = UserSession.getDynamicElements(Constants.GymName)
  status=false;
  showActitivySelectionError=false;
  src;
  isValidEmail = false;
  isEmailAlreadyExist: boolean=false;

  constructor(public modal: Modal,private http_user: HttpUserAuthUtilsService,    private formBuilder: FormBuilder
  
    ,private route: ActivatedRoute,private router: Router,    private common_http: HttpCommonUtilsService,
    private completerService: CompleterService,
    private user_http: HttpUserUtilsService

    ) { }
  

  ngOnInit() {
    this.initTrainerForm();

    this.getParamFromRoute();


    
  }

  getParamFromRoute() {
    this.route.params.subscribe(params => {
      
      if(params['id']&&params['id']!='add'){
         this.currentTrainerId = params['id'] || null;
        this.getuserprofile();

      }else{
     this.initData();
      }
    });
  }
  initData(){
    this.invalidateFileInput();
    this.getPackageList();
    this.getActivitiesList();
    this.getSubCategoryList();
    this.userDetails = LocalStorageService.getUser();

  }
  getuserprofile() {
    this.http_user.getUserProfile(this.currentTrainerId, this, true);
  }
  getPackageList() {
       
    this.http_user.getAllMembershipPackages(this,true,LocalStorageService.getUserId(),true);


}
getSubCategoryList() {
       
  this.http_user.getAllSubCategory(this,true);


}
getActivitiesList() {
  this.common_http.getAllActivity(this, false);
}
  initTrainerForm() {
    this.trainerForm =  this.formBuilder.group({
      trainerName: ['',Validators.required],
      trainerEmail: ['',Validators.required],
      trainerPhone: ['',Validators.required],
      trainerPassword: ['',Validators.required],
      trainerMonthlySalary:['0',Validators.required],
      trainerSalaryDueDate:['1',Validators.required],
      perVisitor: this.formBuilder.array([]),
      perSession: this.formBuilder.array([]),
      gender:['',Validators.required]});
  }
  initPerVisitorForm(element:any) {
 // if(element.noOfClass==Constants.unlimitedClasses){
        //   element.noOfClass = "UNLIMITED CLASSES"
        // }
   let noOfClass =  element.noOfClass==Constants.unlimitedClasses?"Unlimited":element.noOfClass;
   let pakageName =element.name+" ("+element.cost+"KD - "+noOfClass+" Sessions)"
    let revenuePerSession= (element.cost/element.noOfClass).toFixed(2);
    let pervisitorData = this.perVistorDataForPackage(element.id);
    if(element.packageType=="BURN"){
      pakageName = element.name;

    }
   let perVisitorForm =  this.formBuilder.group({
      packageName: pakageName,
      packageId: element.id,
      revenuePerSessionKD:!element.noOfClass|| element.noOfClass==Constants.unlimitedClasses?"-":revenuePerSession+'KD',
      revenuePerSession: !element.noOfClass||element.noOfClass==Constants.unlimitedClasses?"0":revenuePerSession,

      commissionType: pervisitorData? pervisitorData.commissionType:'',
      commisionValue: pervisitorData? pervisitorData.commissionValue:'',
      isAdjustable: pervisitorData? pervisitorData.isAdjustable:false,
      commissionAmount:pervisitorData? pervisitorData.commissionAmount:null,
      id:pervisitorData?pervisitorData.id:null,
      status: true,


    });
   
  return perVisitorForm;
  }
   perVistorDataForPackage(id:any){
    if(this.userData&&this.userData.commissionPerVisitorList){
      let data = this.userData.commissionPerVisitorList.filter((element) => {
        if(element.packageId==id){
          if(element.commissionType=="PERCENTAGE"){
            element.commissionValue = (element.commissionAmount/element.revenuePerSession)*100;
          }else{
            element.commissionValue = +element.commissionAmount;
          }


          return true;
        }
        
      });
      return data[0];
    }else{
      return null;
    }
    
  }
  perSessionDataForPackage(id:any){
    if(this.userData&&this.userData.commissionPerSessionList){
      let data = this.userData.commissionPerSessionList.filter((element) => {
        if(element.subCategoryId==id){
          return true;
        }
        
      });
      return data[0];
    }else{
      return null;
    }
    
  }

  initPerSessionForm(element:any) {
    let perSessionData = undefined;
 if(!element){
  perSessionData  = this.perSessionDataForPackage(undefined);
      let perVisitorForm =  this.formBuilder.group({
        subCategoryId: undefined,
        subCatName: 'Regular Classes',
        perSessionPay: perSessionData? perSessionData.perSessionPay:null,
        id: perSessionData?perSessionData.id:null,
        status: true,
 
        });
        return perVisitorForm;
    }
    perSessionData = this.perSessionDataForPackage(element.id);

    let perVisitorForm =  this.formBuilder.group({
       subCategoryId: element.id,
       subCatName: element.name,
       perSessionPay: perSessionData? perSessionData.perSessionPay:null,
       id: perSessionData?perSessionData.id:null,
       status: true,

       });
   return perVisitorForm;
   }
  get perVisitorArray(): FormArray {
    return this.trainerForm.get('perVisitor') as FormArray;
  }
  get perSessionArray(): FormArray {
    return this.trainerForm.get('perSession') as FormArray;
  }
//**********************************File Upload Section  */
profilePicUpdated(imageResult: ImageResult) {
  if (imageResult.file.type != 'image/gif') {
    this.openImageCropper(imageResult);
  }
}

openImageCropper(imageresult: ImageResult) {

  this.imageCropperSettings.keepAspect = true;
  this.imageCropperSettings.minHeight = 500;
  this.imageCropperSettings.minWidth = 500;
  this.imageCropperSettings.canvasHeight = 550;
  this.imageCropperSettings.canvasWidth = 1000;
  this.imageCropperSettings.width = 500;
  this.imageCropperSettings.height = 500;
  this.imageCropperSettings.croppedHeight = 500;
  this.imageCropperSettings.croppedWidth = 500;
  const parent = this;
  return this.modal.open(ImagePopupComponent, overlayConfigFactory({ imageresult, parent }, BSModalContext));
}

croppedImageResult(type, image) {
  this.uploadfailed = false;
  this.format = false;
  const formData = new FormData();
  // if (!fileList.length) return;
  if (this.profileMainFile) {
    const index = this.profileFileList.indexOf(this.profileMainFile);
    this.profileFileList.splice(index, 1);
  }
  this.profileMainFile = image.image;
  this.profileFileList.push(this.profileMainFile)
  this.profilefilelength = false;
  if (!this.profileFileList.length) { return; }
  this.userImage = image.base64;
  image.name = Date.now().toString().concat(image.extension);
  image.objectType = 'user';
  image.mediaFor = 'UserProfile';
  image.mediaType = 'Image';

  if(this.userData){
    image.objectDataId = this.userData.id;

 
  this.letSelectedImage = image;
  if (this.userData&&this.userData.userProfileImageId) {
    this.uploadImageProgess = true;

    this.http_user.updateMediaJS(this.userData.userProfileImageId, image, this, false)
  }
}

}
 invalidateFileInput() {
  this.fileInput.nativeElement.onclick = () => {
    this.fileInput.nativeElement.value = null;
  };
}

//**********************************File Upload Section  */


addNewActivites($event) {
  this.isFieldValid = true;
  let trainerDetailMappingId = 0;
  let data: ClassCategoryList;

  this.activitiesList.forEach(element => {
    if (element.classCategoryId === $event.originalObject.id) {
      data = element;
    }
  });
  const indexremove = this.deletedActivitiesList1.indexOf($event.originalObject.id);
  if (indexremove > -1) {
    trainerDetailMappingId = this.deletedActivitiesList[indexremove];
  }
  if (!data) {
    data = { 'id': trainerDetailMappingId, 'classCategoryId': $event.originalObject.id, 'classCategoryName': $event.originalObject.name };
  }
  if (indexremove > -1) {
    this.deletedActivitiesList.splice(indexremove, 1);
    this.deletedActivitiesList.splice(indexremove, 1);
  }
  if ($event.originalObject.id && this.activitiesList.indexOf(data) === -1) {
    this.activitiesList.push(data);

  }
}
deleteActivities(value: ClassCategoryList) {
  const index = this.activitiesList.indexOf(value);
  this.activitiesList.splice(index, 1);
  const indexremove = this.deletedActivitiesList.indexOf(value.id);
  if (indexremove == -1) {
    this.deletedActivitiesList.push(value.id);
    this.deletedActivitiesList1.push(value.classCategoryId);
  }
}
onSuccess(type: any, responsedata: any) {
  if (type === Constants.AllActivityListApi) {
    this.activitiesListDataResponse = UserSession.setStatusForList(responsedata.data);
      this.dataService = this.completerService.local(this.activitiesListDataResponse, 'name', 'name')

    }
  if(type==Constants.getPackageListApi){
    this.packageData=[]
    responsedata.data.forEach(element => {
      if(element.packageType=="GENERAL"||element.packageType=="ARIALPACK"||element.packageType=="DROPIN"||element.packageType=="SPECIALCLASSCATEGORY"||element.packageType=="GYMSPECIFIC"||element.packageType=="BURN"||element.packageType=="AUTOJOINWITHPACKAGE"){
        // if(element.noOfClass==Constants.unlimitedClasses){
        //   element.noOfClass = "UNLIMITED CLASSES"
        // }
        if(element.status==true||element.packageType=="BURN"){
          this.packageData.push(element);

        }
      }
      
    });
    this.packageData.forEach(element => {
      this.perVisitorArray.push(this.initPerVisitorForm(element));
      
    });
   
  }
  if(type == Constants.getSubCategoryApi){
    this.subcategoryDataFromApi = responsedata.data;
    this.perSessionArray.push(this.initPerSessionForm(undefined));
    this.subcategoryDataFromApi.forEach(element => {
      this.perSessionArray.push(this.initPerSessionForm(element));
      
    });
  }
  if (type == Constants.FileUploadApi) {
    this.uploadImageProgess = false;
    this.userImage = responsedata.data.thumbnailPath;
   // this.http_user.getUserProfile(LocalStorageService.getUserId(), this, false);
    this.openconfirmpopup(Constants.profilePictureUpdate, Constants.profileUpdate);

  }
  if (type === Constants.mediaUpdate) {
    this.userImage = '';
    this.uploadImageProgess = false;
    this.userImage = responsedata.data.thumbnailPath;

    //this.http_user.getUserProfile(LocalStorageService.getUserId(), this, false);
    this.openconfirmpopup(Constants.profilePictureUpdate, Constants.profileUpdate);

  }
  if (type === Constants.FindUserApi) {
    this.userData = responsedata.data;
    this.trainerForm =  this.formBuilder.group({
      trainerName: ['',Validators.required],
      trainerEmail: ['',Validators.required],
      trainerPhone: ['',Validators.required],
      trainerMonthlySalary:['0',Validators.required],
      trainerSalaryDueDate:['1',Validators.required],
      perVisitor: this.formBuilder.array([]),
      perSession: this.formBuilder.array([]),
      gender:['',Validators.required]});
    this.initData();
   // this.trainerForm.patchValue({trainerPassword:''});
   // this.trainerForm.get('trainerPassword').clearValidators();
    this.trainerForm.updateValueAndValidity();

    this.status = this.userData.trainerAllowEdit;
    this.trainerForm.patchValue({trainerName:this.userData.firstName+' '+this.userData.lastName});
    this.trainerForm.patchValue({trainerEmail:this.userData.email});
    this.trainerForm.patchValue({trainerPhone:this.userData.mobile});
    this.trainerForm.patchValue({gender :this.userData.gender})
   if(this.userData.salaryInfoDto){
    this.trainerForm.patchValue({trainerMonthlySalary:this.userData.salaryInfoDto.salary?this.userData.salaryInfoDto.salary:'0'});
    this.trainerForm.patchValue({trainerSalaryDueDate:this.userData.salaryInfoDto.salaryDay?this.userData.salaryInfoDto.salaryDay:'1'});
   }
    if(this.userData.classCategoryList&&this.userData.classCategoryList.length>0){
      this.userData.classCategoryList.forEach(element => {
        let data = { 'id': element.id, 'classCategoryId': element.classCategoryId, 'classCategoryName': element.classCategoryName };
        this.activitiesList.push(data);
  
      });
    }

    if(this.userData.profileImageThumbnail)
    this.userImage = this.userData.profileImageThumbnail;



  }
  if (type === Constants.RegisterTrainerApi) {
    this.router.navigate(['manageTrainer']);
    if(this.letSelectedImage){
      this.uploadImageProgess = true;

      this.letSelectedImage.objectDataId = this.userData.id;
      this.http_user.uploadMediaJS(this.letSelectedImage, this, false);
  
    }
       

  }
  if(type == Constants.UpdateProfileUser){
    if(this.letSelectedImage){
      this.uploadImageProgess = true;

      this.letSelectedImage.objectDataId = this.userData.id;
      this.http_user.uploadMediaJS(this.letSelectedImage, this, false);
  
    }
    this.router.navigate(['manageTrainer']);

    this.openconfirmpopup(Constants.profileDetailUpdateTrainer, Constants.profileUpdate);

  }
  if (type == Constants.ValidateApi) {
    this.isValidEmail = true;
       this.saveData();
    

}
}
  
onFailure(type: any, response: any) {
  if (type == Constants.ValidateApi) {
       
   
        this.isEmailAlreadyExist=true;
        this.isValidEmail = false;
       
 }
  if (type == Constants.mediaUpdate) {
    this.uploadImageProgess = false;
    this.uploadError = response;
    this.uploadfailed = true;


  }
 

  if (type == Constants.FileUploadApi) {
    this.uploadImageProgess = false;
    this.uploadError = response;
    this.uploadfailed = true;


  }
  if (type === Constants.RegisterTrainerApi) {
alert(response);
  }
  if(type == Constants.UpdateProfileUser){

    this.openconfirmpopup(response,"Error");

  }

}
openconfirmpopup(message, title) {
  return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));

}

selectCommisionType(event,index){
  let value = event.target.value;
  let commisionValue = this.perVisitorArray.controls[index].value.commisionValue;
   if(commisionValue&&+commisionValue>0){
   if(value=="PERCENTAGE"){
    let revenuePerSession = this.perVisitorArray.controls[index].value.revenuePerSession;
    let commisionAmount = (revenuePerSession*commisionValue)/100;
    this.perVisitorArray.controls[index].patchValue({commissionAmount:commisionAmount});
  } else{
    this.perVisitorArray.controls[index].patchValue({commissionAmount:commisionValue});
  }
}
} 
commissionValue(event,index){
  let value = event.target.value;
  let type = this.perVisitorArray.controls[index].value.commissionType;
  if(type=="PERCENTAGE"){
    let revenuePerSession = this.perVisitorArray.controls[index].value.revenuePerSession;
    let commisionAmount = (revenuePerSession*value)/100;
    this.perVisitorArray.controls[index].patchValue({commissionAmount:commisionAmount});
  }else{
  this.perVisitorArray.controls[index].patchValue({commissionAmount:value});
  }
  }
cancelForm(){
  this.router.navigate(['manageTrainer']);

}
saveData(){
  this.isEmailAlreadyExist=false;
  if(this.trainerForm.invalid||this.activitiesList.length==0){
    this.markFormGroupTouched(this.trainerForm);
    this.showActitivySelectionError = this.activitiesList.length==0?true:false;
    return 
  }
  if(this.userData){
    if(!this.isValidEmail&&(this.trainerForm.value.trainerEmail!=this.userData.email)){
      this.common_http.validate('email', this.trainerForm.value.trainerEmail, this, true);
      return
    }
  }else{
    if(!this.isValidEmail){   
      this.common_http.validate('email', this.trainerForm.value.trainerEmail, this, true);
         return
    }
  }


  let data = this.trainerForm.value;
  let perVisitor = data.perVisitor;
  let perSession = data.perSession.filter((element) => {
    if(element.perSessionPay){
      return true;
    }
    

  });
  if(this.userData){
    perVisitor  = data.perVisitor.filter((element) => {
      if(element.id||element.commissionAmount){
        return true;
      }
      
  
    });
    
  }
  if(!this.userData){
    perVisitor  = data.perVisitor.filter((element) => {
      if(element.commissionAmount){
        return true;
      }
      
  
    });
     
  }
 
  if(this.userData){
    let salaryInfo = null;
    if(this.userData.salaryInfoDto){
       salaryInfo= {"salary":data.trainerMonthlySalary,"salaryDay":data.trainerSalaryDueDate,"id":this.userData.salaryInfoDto?this.userData.salaryInfoDto.id:null}

    }else{
      if(+data.trainerMonthlySalary>0){
        salaryInfo= {"salary":data.trainerMonthlySalary,"salaryDay":data.trainerSalaryDueDate}
    }
  }
    let request = {"certificates":"NA",
    "firstName":data.trainerName.split(' ')[0],
    "lastName":data.trainerName.split(' ')[1]?data.trainerName.split(' ')[1]:'',
    "password":data.trainerPassword,
    "gender":data.gender,
    "mobile":data.trainerPhone,
    "email":data.trainerEmail,
    "nationality":"",
    "userType":2,
    "trainerType":"Part a gym",
    "trainerClassRate":"",
    "gymNames":this.gymName,
    "storeId":"1",
    "userDetailMappingList":this.activitiesList,
    "salaryInfo":salaryInfo,
    "commissionPerVisitorList":perVisitor,
    "trainerGymMappingList":LocalStorageService.getTrainerGymMappingList(),
    "commissionPerSessionList":perSession,
    "roles":{"status":this.status},
    "id":this.userData.id,
    "gymDetailId":LocalStorageService.getUserId(),
    "deletedUserDetailIds":this.deletedActivitiesList

    }
    this.http_user.updateuserprofilenew(this.userData.id,request, this, true);
  }else{
    let request = {"certificates":"NA",
    "firstName":data.trainerName.split(' ')[0],
    "lastName":data.trainerName.split(' ')[1]?data.trainerName.split(' ')[1]:'',
    "password":data.trainerPassword,
    "gender":data.gender,
    "mobile":data.trainerPhone,
    "email":data.trainerEmail,
    "nationality":"",
    "userType":2,
    "trainerType":"Part a gym",
    "trainerClassRate":"",
    "gymNames":this.gymName,
    "storeId":"1",
    "userDetailMappingList":this.activitiesList,
    "salaryInfo":+data.trainerMonthlySalary>0?{"salary":data.trainerMonthlySalary,"salaryDay":data.trainerSalaryDueDate}:null,
   "commissionPerVisitorList":perVisitor,
    "trainerGymMappingList":LocalStorageService.getTrainerGymMappingList(),
    "commissionPerSessionList":perSession,
    "roles":{"status":this.status},
    "gymDetailId":LocalStorageService.getUserId()
    }
    this.user_http.registerTrainerNew(request, this, true)

  }
}
onCheckBoxCheked(event){
  this.status = event.target.checked;
}
private markFormGroupTouched(formGroup: FormGroup) {
  try {
    (<any>Object).values(formGroup.controls).forEach(control => {
      control.markAsTouched();
      if (control.controls) {
        control.controls.forEach(c => this.markFormGroupTouched(c));
      }
    });
  } catch (ex) { }
}
}







